@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100;200;300;400;500&display=swap);
*, *:before, *:after{
  padding: 0;
  margin: 0;
  list-style: none;
  text-decoration: none;
  box-sizing: border-box;
}
::-webkit-scrollbar{
  display: none;
}
.menu{
    width: 15%;
    height: 100vh;
    background-color: white;
    box-shadow: 0px 0px 10px rgb(214, 213, 213);
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.menu-nav{
    margin-top: -17.5rem;
    padding: 0 2rem;
}
.menu-nav ul{
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.menu-nav ul a{
    padding: .7rem 1rem;
    border-radius: 7px;
    margin-top: .5rem;
    cursor: pointer;
    font-weight: 500;
    transition: 200ms ease-out;
}
.menu-nav ul a:hover{
    background-color: rgb(220, 255, 250); 
    color:rgb(29, 162, 185);
}
.media-container{
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding: 1rem;
    width: 100%;
}
.media-container > *{
    font-size: 2rem;
    color: rgb(29, 162, 185);
    cursor: pointer;
}
.menu-media{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-bottom: 1rem;
}
.menu-media > img{
    width: 80%;
    -webkit-filter: grayscale(.7);
            filter: grayscale(.7);
}
.home{
    padding: 3rem 5rem;
    width: 85%;
}
.home h2{
    font-weight: 500;
}
.home-content{
    padding: 3rem 0;
    display: grid;
    grid-gap: 3rem 4rem;
    grid-template-columns: repeat(2, 1fr);
    text-align: justify;
    line-height: 1.5;
}
.home-ph > p{
    margin-top: 1rem;
}
.home-img > img{
    object-fit: cover;
    border-radius: 10px;
    box-shadow: 0px 0px 5px rgb(187, 187, 187);
    width: 100%;
    height: 100%;
}
.home-img{
    position: relative;
    overflow: hidden;
    cursor: pointer;
    border-radius: 10px;
    box-shadow: 0px 0px 7px gray;
}
.home-img p{
    position: absolute;
    left: 0;
    right: 0;
    bottom: -7rem;
    background: rgba(0, 0, 0, 0.5);
    border-radius: 0 0 10px 10px;
    padding: 2rem;
    transition: 300ms ease-in-out;
    color: white;
    font-weight: 300;
}
.home-img:hover p{
    bottom: 0;
}
.about{
    padding: 3rem 5rem;
    width: 85%;
}
.about h2{
    font-weight: 500;
}
.about >p{
    margin-top: 1rem;
}
.about-content{
    padding: 3rem;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
}
.about-content img{
    object-fit:contain;
    margin-top: 2rem;
    border-radius: 7px;
    box-shadow: 0px 0px 5px rgb(110, 109, 109);
}
.about-content h3{
    font-weight: 500;
}
.about-text{
    padding: 0 2rem;
}
.about-text p{
    margin-top: 2rem;
    font-weight: 300;
    font-size: 1.2rem;
}
.writing{
    padding: 3rem 5rem;
    width: 85%;
}
.writing-content{
    padding: 3rem;
}
.project{
    display: flex;
    padding: 2rem;
}
.project img{
    border-radius: 10px;
    box-shadow: 0px 0px 7px gray;
}
.project-text{
    padding: 0 2rem;
}
.project-text p{
    margin-top: 1rem;
    text-align: justify;
}

.art{
    padding: 5rem 3rem;
    width: 85%
}
body{
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  background-color: rgb(243, 243, 243);
  padding: 0 2vw;
}
.App{
  display: flex;
}
a,h1,h2,h3,h4,p{
  font-family: 'Montserrat', sans-serif;
  text-shadow: none;
}
.home, .about, .writing, .art, .contact, .sounds, .videos{
  animation: appearing;
  animation-duration: 1000ms;
  animation-fill-mode: forwards;
  animation-timing-function: cubic-bezier(.85,.63,.58,1.17);
  height: 100vh;
  overflow-y: scroll;
} 
@keyframes appearing{
  from{
    transform: translateY(100vh);
    /* background-color: rgb(42, 241, 241); */
    border-radius: 10px;
  }
  to{
    transform: translateX(0);
    background-color: transparent;
  }
}
